.container_aside button, .container_aside_content, .container_aside_title {
  font-family: "Roboto";
  font-style: normal;
}

.container_aside button {
  background: #E4C1AE;
  border-radius: 32px;
  width: 128px;
  height: 48px;
}

.container {
  position: relative;
  display: flex;
  width: 1440px;
  height: 960px;
  background: #FFFFFF;
}
.container_image {
  position: absolute;
  width: 505px;
  height: 330px;
  left: 230px;
  top: 96px;
}
.container_aside {
  position: absolute;
  width: 380px;
  height: 250px;
  left: 830px;
  top: 136px;
}
.container_aside_title {
  width: 380px;
  height: 50px;
  left: calc(50% - 190px + 300px);
  top: calc(50% - 25px - 319px);
  color: #121212;
  font-size: 42px;
  font-weight: 700;
  line-height: 120%;
}
.container_aside_content {
  width: 380px;
  height: 96px;
  left: calc(50% - 190px + 300px);
  top: calc(50% - 48px - 222px);
  color: #121212;
  font-size: 16px;
  font-weight: 300;
  line-height: 150%;
}
.container_aside button {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 16px 40px;
  gap: 10px;
  left: 830px;
  top: 338px;
  border: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #FFFFFF;
}